.button {
  display: inline-block;
  padding: 16px 30px;
  font-size: 13px;
  line-height: 16px;
  font-family: 'Gilroy', Arial, sans-serif;
  color: #fff;
  background-color: #0072c7;
  border: 1px solid #0072c7;
  border-radius: 2px;
  white-space: nowrap;
  cursor: pointer;
  box-sizing: border-box;
}
.button:hover {
  color: #fff;
}
.button:focus {
  outline: none;
  background-color: #0072c7;
}
.menu__wrapper {
  width: 100%;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 13px;
  line-height: 15px;
  color: #3a393c;
  box-sizing: border-box;
  position: fixed;
  left: 0;
  z-index: 99;
  background-color: #fff;
  transition: top, 1s;
}
.menu__items {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  max-width: 1570px;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}
.menu__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  margin-right: 35px;
  padding: 10px 0;
  cursor: pointer;
  box-sizing: border-box;
  position: relative;
}
.menu__item.active .sub-menu__items {
  display: block;
}
.menu__item__main {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 100%;
}
.menu__item .sub-menu__items {
  position: absolute;
  top: 80%;
  left: 10px;
  border: 1px solid #f0f0f0;
  box-sizing: border-box;
  border-radius: 2px;
  box-shadow: 3px 3px 4px rgba(215,215,215,0.2);
  background-color: #fff;
  display: none;
  z-index: 200;
}
.menu__item .sub-menu__items:hover {
  display: block;
}
.menu__item .sub-menu__items a {
  display: block;
}
.menu__item .sub-menu__item {
  display: block;
  padding: 10px 36px 10px 20px;
  font-size: 13px;
  line-height: 15px;
  color: #3a393c;
  font-family: 'Roboto', Arial, sans-serif;
  cursor: pointer;
  white-space: nowrap;
}
.menu__item .sub-menu__item:hover {
  background-color: #f7f7f7;
}
@media (max-width: 450px) {
  .menu__item {
    margin-right: 0;
  }
}
@media (max-width: 1279px) {
  .menu__item .sub-menu__items {
    left: auto;
    right: 0;
  }
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__item:hover {
  color: #3a393c;
}
.menu__item__icon {
  margin-right: 8px;
}
.menu__item__icon img {
  width: 100%;
  height: auto;
}
.menu__item__text {
  color: #3a393c;
}
.menu__item__text + .menu__item__icon {
  margin-left: 12px;
  margin-right: 0;
}
.menu__item__logo {
  flex-grow: 1;
}
.menu__item.menu__item-vr .menu__item__text {
  color: #0072c7;
}
.menu__item.btn-menu {
  padding: 26px 15px;
  background-color: #f8f8f8;
}
.menu__item.btn-menu .menu__item__icon {
  width: 20px;
  min-width: 20px;
  margin-left: 0;
}
.menu__item.btn-menu .menu__item__icon img {
  width: 100%;
  height: auto;
}
.menu__item.btn-menu .menu__item__text {
  font-size: 14px;
  line-height: 17px;
  margin-right: 10px;
}
